  font-display: swap;
  src:
    url("../fonts/.woff2") format("woff2");
} */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "Mono", "Times New Roman", serif;
  font-style: normal;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 15px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  color: #F2F5F7;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  padding-top: 30px;
  padding-bottom: 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  width: 100%;
  z-index: 3;
}
@media (max-width: 1023px) {
  .header {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-left: 15px;
}

.header__logo-text {
  color: #F2F5F7;
  display: flex;
  flex-direction: column;
  margin: 0 0 0 15px;
  padding: 0;
}
@media (max-width: 767px) {
  .header__logo-text {
    display: none;
  }
}

@media (max-width: 767px) {
  .header__logo-img {
    width: 35px;
    height: 35px;
  }
}

.header__logo-name {
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  margin-bottom: 5px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
}

.header__logo-description {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.7);
}

.header__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(2, 225px);
}
@media (max-width: 1023px) {
  .header__list {
    display: flex;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .header__list {
    margin-right: 10px;
  }
}

.header__link {
  color: #F2F5F7;
  line-height: 18px;
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__link {
    padding: 10px;
    margin-left: 20px;
  }
}

.header__link-text {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .header__link-text {
    display: none;
  }
}

.header__button {
  border: 1px solid #F2F5F7;
  border-radius: 3px;
}
@media (max-width: 1023px) {
  .header__button {
    display: none;
  }
}

.intro {
  background-image: url("../img/background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 30%;
  min-height: 880px;
  color: #F2F5F7;
}
@media (max-width: 1023px) {
  .intro {
    min-height: 640px;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 560px;
  }
}

.intro__container {
  padding-top: 305px;
  margin-left: 25%;
}
@media (max-width: 1023px) {
  .intro__container {
    margin-left: 0;
    padding-top: 180px;
  }
}
@media (max-width: 767px) {
  .intro__container {
    padding-top: 150px;
  }
}

.intro__title {
  padding: 0;
  margin: 0 0 40px;
  font-style: normal;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .intro__title {
    margin: 0 0 20px;
    font-size: 64px;
    line-height: 75px;
  }
}
@media (max-width: 767px) {
  .intro__title {
    font-size: 36px;
    line-height: 42px;
  }
}

.intro__description {
  padding: 0;
  margin: 0 0 40px;
  font-weight: 400;
  font-size: 22px;
  line-height: 34px;
}
@media (max-width: 1023px) {
  .intro__description {
    margin: 0 0 65px;
  }
}
@media (max-width: 767px) {
  .intro__description {
    font-size: 14px;
    line-height: 22px;
  }
}

.intro__link {
  background-color: #00B2FF;
  padding: 15px 25px 18px;
  color: inherit;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .intro__link {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .intro__link {
    font-size: 14px;
    line-height: 16px;
    margin-left: 0;
  }
}
.intro__link--mobile {
  display: none;
}
@media (max-width: 767px) {
  .intro__link--mobile {
    display: block;
  }
}
@media (max-width: 767px) {
  .intro__link--desktop {
    display: none;
  }
}
@media (hover: hover) {
  .intro__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.intro__link.focus-visible:focus {
  opacity: 0.8;
}

.advantages {
  padding-top: 120px;
  background-color: #F2F5F7;
}
@media (max-width: 1023px) {
  .advantages {
    padding-top: 80px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 50px;
  }
}

.advantages__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
@media (max-width: 767px) {
  .advantages__list {
    display: block;
  }
}

.advantages__item {
  position: relative;
  padding: 45px 40px;
  min-height: 300px;
  border: 1px solid #EBEBEB;
}
.advantages__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5px;
  background-color: #00B2FF;
}
.advantages__item:hover {
  background-color: #00B2FF;
  cursor: pointer;
  color: #ffffff;
}
.advantages__item:hover svg {
  fill: #ffffff;
}
.advantages__item:hover h3 {
  color: #ffffff;
}
@media (max-width: 767px) {
  .advantages__item {
    min-height: 325px;
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__item:last-of-type {
    margin-bottom: 0;
  }
}

.advantages__icon {
  margin-bottom: 20px;
  fill: #00B2FF;
}

.advantages__item-title {
  padding: 0;
  margin: 0 0 15px;
  color: #00B2FF;
  font-size: 24px;
  line-height: 34px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-weight: 400;
}

.advantages__description {
  padding: 0;
  margin: 0;
}

.info {
  padding-top: 120px;
  padding-bottom: 100px;
  background-color: #F2F5F7;
  position: relative;
}
.info::before {
  position: absolute;
  content: "";
  left: 30px;
  top: 120px;
  width: 150px;
  height: 320px;
  background-image: url("../img/2022.png");
}
@media (max-width: 1023px) {
  .info::before {
    top: 80px;
  }
}
@media (max-width: 767px) {
  .info::before {
    display: none;
  }
}
@media (max-width: 1023px) {
  .info {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .info {
    padding-top: 50px;
  }
}

.info__container {
  color: #041827;
  margin-left: 250px;
  width: fit-content;
}
@media (max-width: 1023px) {
  .info__container {
    margin-left: 180px;
  }
}
@media (max-width: 767px) {
  .info__container {
    margin-left: 0;
  }
}

.info__title {
  margin: 0 0 15px;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
}
@media (max-width: 767px) {
  .info__title {
    font-size: 28px;
    line-height: 39px;
  }
}

.info__description {
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .info__description {
    margin-bottom: 20px;
  }
}

.info__text {
  padding: 0;
  margin: 0 0 20px;
}
@media (max-width: 767px) {
  .info__text {
    font-size: 14px;
    line-height: 19px;
  }
}

.info__text-container {
  transition: all;
}

.info__button {
  padding: 14px 13px;
  color: #00B2FF;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  display: block;
  text-transform: uppercase;
  border: 1px solid #00B2FF;
  border-radius: 3px;
  min-width: 180px;
  min-height: 45px;
}
@media (max-width: 767px) {
  .info__button {
    min-width: 160px;
  }
}

.footer {
  background-color: #041827;
  padding-top: 55px;
  padding-bottom: 55px;
}

.footer__wrapper {
  display: flex;
  justify-content: center;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    display: block;
  }
}

.footer__container {
  position: relative;
  display: flex;
}
@media (max-width: 767px) {
  .footer__container {
    display: block;
  }
}
@media (max-width: 767px) {
  .footer__container::before {
    position: absolute;
    content: "";
    width: 1000%;
    height: 1px;
    top: -20px;
    left: -100%;
    background-color: rgba(248, 253, 255, 0.7);
  }
