    height: 1px;
    bottom: 0;
    left: -100%;
    background-color: rgba(248, 253, 255, 0.7);
  }
}
@media (max-width: 1023px) {
  .footer__block {
    margin-right: 130px;
  }
}
@media (max-width: 767px) {
  .footer__block {
    margin-top: 20px;
    margin-right: 0;
    max-width: none;
  }
}

.footer__title {
  position: relative;
  padding: 0;
  margin: 0 0 20px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  transition: opacity 0.3s ease;
