     @include vp-767 {
       &::after {
         display: none;
       }
     }
   } */
}
@media (max-width: 767px) {
  .footer__title {
    padding-bottom: 20px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .footer__title:hover, .footer__title:active {
    cursor: pointer;
    opacity: 0.6;
  }
}

.footer__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.footer__list--section {
  display: grid;
  grid-template-columns: repeat(2, 220px);
  gap: 30px;
}
@media (max-width: 1023px) {
  .footer__list--section {
    display: block;
  }
}
.footer__list--contacts {
  max-width: 345px;
}

.footer__item {
  margin-bottom: 10px;
}
.footer__item:last-of-type:not(.footer__item--not-last) {
  margin-bottom: 0;
}
@media (max-width: 767px) {
  .footer__item:last-of-type:not(.footer__item--not-last) {
    padding-bottom: 20px;
  }
}

.footer__link {
  font-size: 16px;
  line-height: 22px;
  color: rgba(255, 255, 255, 0.7);
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.8;
}

.footer__section-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.social__container {
  margin-right: 130px;
}
@media (max-width: 1023px) {
  .social__container {
    display: flex;
    justify-content: space-between;
    margin: 0 0 35px;
  }
}
@media (max-width: 767px) {
  .social__container {
    display: block;
  }
}

.social__logo {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.social__logo-text {
  color: #F2F5F7;
  display: flex;
  flex-direction: column;
  margin: 0 0 0 15px;
  padding: 0;
}

.social__logo-name {
  font-weight: 700;
  font-size: 25px;
  line-height: 29px;
  margin-bottom: 5px;
  font-family: "Roboto-Condensed", "Arial", sans-serif;
}

.social__logo-description {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto", "Arial", sans-serif;
  color: rgba(255, 255, 255, 0.7);
}

.social__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
}

.social__item {
  margin-left: 15px;
}

.social__icon {
  fill: #ffffff;
  padding: 5px;
}

.accordion__header {
  position: relative;
}
@media (max-width: 767px) {
  .accordion__header {
    cursor: pointer;
  }
}
.accordion__header::after, .accordion__header::before {
  position: absolute;
  content: "";
  background-color: #F2F5F7;
  transition: transform 0.2s ease-out;
}
.accordion__header::before {
  top: 7px;
  width: 15px;
  height: 1px;
  right: 26px;
}
.accordion__header::after {
  top: 0;
  width: 1px;
  height: 15px;
  right: 33px;
}

.accordion__item-show .accordion__header::after,
.accordion__item-slidedown .accordion__header::after {
  transform: rotate(-90deg);
}

@media (max-width: 767px) {
  .accordion__item:not(.accordion__item-show) .accordion__body {
    display: none;
  }
}

